import React, { useState, useEffect } from "react";

const Device = props => {
  window.onclick = function(event) {
    const modal = this.document.querySelector(".form .modal");

    if (event.target === modal) {
      props.setModal(-1);
    }
  };

  const [device, setDevice] = useState({
    newDevice: false,
    model: "",
    count: 1
  });
  const [devices, setDevices] = useState([]);

  const validateMessages = { model: "" };

  const preventPageRefresh = () => {
    const buttons = document.querySelectorAll(".device button");

    buttons.forEach(element => {
      element.addEventListener("click", e => {
        e.preventDefault();
      });
    });
  };
  const validate = () => {
    if (!device.model.trim()) {
      validateMessages["model"] = "To pole nie może być pute";
      return false;
    } else {
      validateMessages["model"] = "";
      return true;
    }
  };
  const showValidateInfo = () => {
    const messagePlace = document.querySelector(".device .validate-message");
    messagePlace.innerHTML = validateMessages.model;
  };

  const handleCloseModal = () => {
    props.setModal(-1);
  };
  const handleChangeDevice = e => {
    setDevice({ ...device, [e.target.name]: e.target.value });
  };
  const handleAddDevice = (newDevice = false) => {
    if (validate()) {
      const toAddDevice = { ...device, newDevice };
      setDevices([...devices, toAddDevice]);
    } else {
      showValidateInfo();
    }
  };
  const handleDelete = e => {
    const id = e.target.getAttribute("data-target");
    const newDevicesArray = devices.splice(id, 1);

    setDevices(newDevicesArray);
  };

  useEffect(() => {
    preventPageRefresh();
  });

  const deviceItems = devices.map((deviceItem, index) => {
    return (
      <div key={index} className="item btn btn-info m-1">
        <p>
          {deviceItem.model} <i class="fas fa-times"></i> {deviceItem.count}
        </p>
        <p>{deviceItem.newDevice ? "Nowe urządzenie" : "Moje urządzenie"}</p>
        <button
          className="btn btn-danger"
          onClick={handleDelete}
          data-target={index}
        >
          Usuń
        </button>
      </div>
    );
  });

  return (
    <div
      className="modal fade show"
      tabIndex="-1"
      role="dialog"
      style={{ display: "block" }}
      aria-modal="true"
    >
      <div className="modal-dialog" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Dodaj urządzenia</h5>
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={handleCloseModal}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <div className="device">
              <div className="device-data">
                <div className="form-group">
                  <label htmlFor="model">Model:</label>
                  <input
                    type="text"
                    name="model"
                    id="model"
                    className="form-control"
                    value={device.model}
                    onChange={handleChangeDevice}
                  />
                  <span className="validate-message text-danger"></span>
                </div>
                <div className="form-group">
                  <label htmlFor="count">Ilość:</label>
                  <input
                    className="form-control"
                    type="number"
                    name="count"
                    value={device.count}
                    onChange={handleChangeDevice}
                  />
                </div>
              </div>
              <div className="text-center">
                <button
                  className="btn btn-info m-2"
                  onClick={handleAddDevice.bind(null, true)}
                >
                  Zamów nowe urządzenia
                </button>
                <button
                  className="btn btn-info mx-2"
                  onClick={handleAddDevice.bind(null, false)}
                >
                  Dodaj swoje urządzenia
                </button>
              </div>
            </div>
          </div>
          {deviceItems}
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-dismiss="modal"
              onClick={handleCloseModal}
            >
              Anuluj
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => {
                // handleSaveData();
                handleCloseModal();
              }}
            >
              Zapisz zmiany
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Device;
