import $ from "jquery";
import React, { useEffect, useState } from "react";

const POS = props => {
  window.onclick = function(event) {
    const modal = this.document.querySelector(".form .modal");

    if (event.target === modal) {
      props.setModal(-1);
    }
  };

  const [seats, setSeats] = useState([]);
  const [seat, setSeat] = useState({ name: "", pos: false, newPos: false });
  const [S4HPOS, setS4HPOS] = useState({
    seats,
    storages: 0,
    printedInKitchen: false,
    isComputerNetwork: false,
    invoiceModule: false,
    cashBankModule: false,
    isWifiWithGoodRange: false,
    workersTablets: 0
  });
  const [addStorages, setAddStorages] = useState(false);
  const [mounted, setMounted] = useState(false);
  const handleDataChange = event => {
    if (event.target.getAttribute("type") === "checkbox") {
      setS4HPOS({ ...S4HPOS, [event.target.name]: event.target.checked });
    } else {
      setS4HPOS({ ...S4HPOS, [event.target.name]: event.target.value });
    }
  };

  const handleSaveData = () => {
    props.setForm(form => ({ ...form, S4HPOS }));
  };

  const handleDeleteSeat = event => {
    const id = event.target.getAttribute("data-target-id");
    const newSeats = [...seats];
    console.log(newSeats);
    newSeats.splice(id, 1);

    setSeats(newSeats);
  };

  const ShowModal = () => {
    $(`#question-${props.id}`).show();
  };

  const hideElements = () => {
    if (!mounted) {
      document.querySelectorAll(".form .hide-me").forEach(element => {
        $(element).hide();
      });
      setMounted(true);
    }
  };

  const handleChangeAddStorage = e => {
    setAddStorages(e.target.checked);
  };

  const preventPageRefresh = () => {
    document.querySelectorAll(".form button").forEach(button => {
      button.addEventListener("click", event => {
        event.preventDefault();
      });
    });
  };

  const handleCloseModal = () => {
    props.setModal(-1);
  };

  const handleAddSeats = event => {
    event.preventDefault();

    if (seatIsValid()) {
      setSeats([...seats, seat]);
      setSeat({ name: "", pos: false, newPos: false });
    }

    console.log(seats);
  };

  const seatIsValid = () => {
    let valid = false;

    if (!seat.name.trim()) {
      const seatValidator = document.querySelector(".form .seat-validator");
      seatValidator.innerHTML = "To pole nie może być puste.";

      valid = false;
    } else {
      const seatValidator = document.querySelector(".form .seat-validator");
      seatValidator.innerHTML = "";
      console.log("true");

      valid = true;
    }

    return valid;
  };

  const handleSeatsOnChange = event => {
    if (event.target.getAttribute("type") === "checkbox") {
      setSeat({ ...seat, [event.target.name]: event.target.checked });
    } else {
      setSeat({ ...seat, [event.target.name]: event.target.value });
    }
  };

  useEffect(() => {
    ShowModal();
    hideElements();
    preventPageRefresh();
    document.body.style.overflow = "hidden";

    return () => {
      document.body.style.overflow = "auto";
    };
  });

  const seatsItems = seats.map((seat, index) => {
    return (
      <div
        key={`seat-${index}`}
        className="btn btn-info my-2 seat text-left"
        id={`sead-id-${index}`}
      >
        <p className="name m-0 font-weight-bold">{seat.name}</p>
        <p className="m-0 d-flex w-100 justify-content-between">
          Stanowisko jest POS-em?
          {seat.pos ? (
            <i className="text-right fas fa-check ml-1"></i>
          ) : (
            <i className="text-right fas fa-times ml-1"></i>
          )}
        </p>
        <p className="m-0 d-flex w-100 justify-content-between">
          Potrzebny nowy POS?
          {seat.newPos ? (
            <i className="text-right fas fa-check ml-1"></i>
          ) : (
            <i className="text-right fas fa-times ml-1"></i>
          )}
        </p>
        <button
          data-target-id={index}
          onClick={handleDeleteSeat}
          className="btn btn-danger w-100"
        >
          Usuń stanowisko
        </button>
      </div>
    );
  });

  return (
    <div
      className="modal fade show"
      tabIndex="-1"
      role="dialog"
      id={`question-${props.id}`}
      style={{ display: "block" }}
      aria-modal="true"
    >
      <div className="modal-dialog" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">S4H POS</h5>
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={handleCloseModal}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <div className="seats border p-2">
              <div className="form-group">
                <label htmlFor={`seat-name-${props.id}`}>
                  Nazwa stanowiska
                </label>
                <input
                  type="text"
                  name="name"
                  id={`seat-name-${props.id}`}
                  className="form-control"
                  value={seat.name}
                  onChange={handleSeatsOnChange}
                />
              </div>
              <span className="seat-validator text-danger"></span>
              <div className="form-group">
                <input
                  type="checkbox"
                  name="pos"
                  id={`is-pos-termial${props.id}`}
                  className="mr-1"
                  checked={seat.pos}
                  onChange={handleSeatsOnChange}
                />
                <label htmlFor={`is-pos-termial${props.id}`}>
                  Czy stanowisko jest terminalem POS?
                </label>
              </div>
              <div className="form-group">
                <input
                  type="checkbox"
                  id={`new-pos${props.id}`}
                  name="newPos"
                  className="mr-1"
                  checked={seat.newPos}
                  onChange={handleSeatsOnChange}
                />
                <label htmlFor={`new-pos${props.id}`}>
                  Czy potrzebny nowy terminal?
                </label>
              </div>
              <button className="btn btn-info" onClick={handleAddSeats}>
                Dodaj stanowisko
              </button>
              <div className="">{seatsItems}</div>
            </div>

            <div className="form-group">
              <input
                type="checkbox"
                name="printedInKitchen"
                value={S4HPOS.printedInKitchen}
                id={`printed-in-kitchen${props.id}`}
                className="mr-1"
                onChange={handleDataChange}
              />
              <label htmlFor={`printed-in-kitchen${props.id}`}>
                Czy zamówieniasą drukowane boniarkach w kuchni?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                name="isComputerNetwork"
                id={`is-computer-network${props.id}`}
                value={S4HPOS.isComputerNetwork}
                className="mr-1"
                onChange={handleDataChange}
              />
              <label htmlFor={`is-computer-network${props.id}`}>
                Czy w lokalu jest sieć komputerowa?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                name="invoiceModule"
                id={`invoice-module${props.id}`}
                value={S4HPOS.invoiceModule}
                className="mr-1"
                onChange={handleDataChange}
              />
              <label htmlFor={`invoice-module${props.id}`}>
                Chcesz dodać moduł fakturowania?
              </label>
              <button
                type="button"
                className="tooltip-button"
                data-toggle="tooltip"
                data-placement="top"
                title="Moduł faktur VAT"
              >
                <i className="fas fa-info-circle ml-1"></i>
              </button>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`cash-bank-module${props.id}`}
                className="mr-1"
                value={S4HPOS.cashBankModule}
                name="cashBankModule"
                onChange={handleDataChange}
              />
              <label htmlFor={`cash-bank-module${props.id}`}>
                Chcesz dodać moduł Kasa/Bank?
              </label>
              <button
                type="button"
                className="tooltip-button"
                data-toggle="tooltip"
                onChange={handleDataChange}
                data-placement="top"
                title="Wykonywanie wpłat i wypłat do kasy lub operacji przelewów .Przy zakupie FAKTUROWANIA lub KART RABATOWYCH cena Kasa/Bank wynosi 1zł"
              >
                <i className="fas fa-info-circle ml-1"></i>
              </button>
            </div>
            <div className="form-group">
              <label htmlFor={`workers-tablets-${props.id}`}>
                Czy obsługa przyjmuje zamówienia na tabletach? (podaj ilość)
              </label>

              <input
                type="number"
                name="workersTablets"
                value={S4HPOS.workersTablets}
                id={`workers-tablets-${props.id}`}
                className="form-control"
                min="0"
                onChange={handleDataChange}
              />
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                onChange={handleDataChange}
                name="isWifiWithGoodRange"
                id={`is-wifi${props.id}`}
                value={S4HPOS.isWifiWithGoodRange}
                className="mr-1"
              />
              <label htmlFor={`is-wifi${props.id}`}>
                Czy w lokalu jest WiFi z bardzo dobrym zasięgiem, który obejmuje
                także sprzedaż poza lokalem np.. ogródek, taras?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`add-storages${props.id}`}
                className="mr-1"
                checked={addStorages}
                onChange={handleChangeAddStorage}
              />
              <label htmlFor={`add-storages${props.id}`}>
                Czy chcesz dodać magazyny w następnym kroku?
              </label>
            </div>
            <div className="form-group">
              <button className="btn btn-info">Dodaj artykuły</button>
            </div>
          </div>

          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-dismiss="modal"
              onClick={handleCloseModal}
            >
              Anuluj
            </button>
            {addStorages ? (
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => {
                  handleSaveData();
                  props.setModal(1);
                }}
              >
                Przejdź do S4H CHEF
              </button>
            ) : (
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => {
                  handleSaveData();
                  handleCloseModal();
                }}
              >
                Zapisz zmiany
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default POS;
