import React, { useState } from "react";
import Device from "./Device";

const FiscalDevices = props => {
  const offers = [
    { descripton: "", name: "Kasa / drukarka fiskalna" },
    { descripton: "", name: "Fiskalizacja" }
  ];
  const [modal, setModal] = useState(-1);
  const modalIdMapper = {
    0: (
      <Device
        setModal={setModal}
        id={modal}
        setForm={props.setForm}
        form={props.form}
      />
    ),
    1: null
  };

  const offerItems = offers.map((offer, index) => {
    return (
      <div
        key={index}
        className="offer col-4 mb-1 p-0 d-flex flex-column justify-content-between border mb-2"
      >
        <div className="title">
          <h3 className="text-center">{offer.name}</h3>
        </div>
        <div className="description p-2 text-center">{offer.description}</div>
        <div>
          <button
            type="button"
            className="btn btn-primary w-100"
            data-toggle="modal"
            data-target={`#fiscal-devices-info-${index}`}
          >
            Zobacz informacje
          </button>
          <button
            data-target-id={index}
            className="btn btn-success w-100 configure"
            onClick={e => {
              e.preventDefault();
              setModal(index);
            }}
          >
            Dodaj
          </button>
          <div
            className="modal fade"
            id={`fiscal-devices-info-${index}`}
            tabIndex="-1"
            role="dialog"
            aria-labelledby="modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="modalLabel">
                    {offer.name}
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">...</div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                  >
                    Zamknij
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="fiscal-devices">
      <button
        className="expand-button mb-2"
        type="button"
        data-target="#expand-fiscal-devices"
      >
        Urządzenia fiskalne
      </button>
      <div
        id="expand-fiscal-devices"
        className="row justify-content-around m-0 collapse-me"
      >
        {offerItems}
      </div>
      {modalIdMapper[modal]}
    </div>
  );
};

export default FiscalDevices;
