import React, { useState, useEffect } from "react";

const Reciepe = props => {
  const [recipes, setRecipes] = useState([]);
  const [recipe, setRecipe] = useState({ name: "", details: "" });
  const handleClose = e => {
    e.preventDefault();
    props.hideRecipeForm();
  };

  const handleAdd = e => {
    e.preventDefault();
    const splittedDetails = recipe.details.split(",");
    splittedDetails.forEach((element, index) => {
      splittedDetails[index] = element.trim();
    });
    const customizedRecipe = { name: recipe.name, details: splittedDetails };

    setRecipes([...recipes, customizedRecipe]);
    props.setCHEF({ ...props.CHEF, recipes });

    setRecipe({ name: "", details: "" });
  };

  const handleChangeInputValue = e => {
    setRecipe({ ...recipe, [e.target.name]: e.target.value });
  };

  const handleDelete = e => {
    e.preventDefault();
    const id = e.target.getAttribute("data-target");

    const newRecipesArray = recipes.splice(id, 0);
    setRecipes(newRecipesArray);
  };

  const handleEdit = e => {
    e.preventDefault();

    const id = e.target.getAttribute("data-target");
    setRecipe({
      name: recipes[id].name,
      details: recipes[id].details.join(", ")
    });

    const newRecipesArray = recipes.splice(id, 0);
    setRecipes(newRecipesArray);
  };

  const myRecipes = recipes.map((element, index) => (
    <div
      key={index}
      className="my-recipes btn btn-info d-block text-white mt-2"
    >
      <p>
        {element.name}: {element.details.join(", ")}
      </p>
      <button
        data-target={index}
        className="btn btn-danger"
        onClick={handleDelete}
      >
        Usuń
      </button>
      <button
        data-target={index}
        onClick={handleEdit}
        className="btn btn-success"
      >
        Edytuj
      </button>
    </div>
  ));

  return (
    <div className="recipe">
      <div className="form-group">
        <label htmlFor="product-name">Nazwa artykułu:</label>
        <input
          className="form-control"
          type="text"
          id="product-name"
          name="name"
          value={recipe.name}
          onChange={handleChangeInputValue}
        />
      </div>
      <div className="form-group">
        <label htmlFor="product-recipe">
          Wypisz po przcecinku listę składników
        </label>
        <textarea
          className="form-control"
          name="details"
          id="product-recipe"
          cols="30"
          rows="10"
          value={recipe.details}
          onChange={handleChangeInputValue}
        ></textarea>
      </div>
      <button className="btn btn-success" onClick={handleAdd}>
        Dodaj
      </button>
      <button className="btn btn-info" onClick={handleClose}>
        Zamknij formularz dodawania receptur
      </button>
      <div className="recipes">{myRecipes}</div>
    </div>
  );
};

export default Reciepe;
