import "./styles/css/style.css";
import React, { useEffect, useState } from "react";
import Form from "./components/Form";
import Contact from "./components/Contact";
import SoftwareImplementation from "./components/SoftwareImlpementations/SofwareImplementation";
import SoftwareLearn from "./components/SoftwareLearn/SoftwareLearn";
import FiscalDevices from "./components/FiscalDevices/FiscalDevices";
import $ from "jquery";

function App() {
  const [form, setForm] = useState({});
  const handleShowData = e => {
    console.log("aktalne dane: ", form);
    e.preventDefault();
  };

  const moveNext = (currentForm, nextForm) => {
    const next = document.querySelector(nextForm);
    const current = document.querySelector(currentForm);
    $(next).show();
    $(current).hide();
    window.scrollTo(0, 0);
  };

  useEffect(() => {
    const elements = document.querySelectorAll(".form .collapse-me");
    elements.forEach(element => {
      $(element).hide();
      element.classList.remove("collapse-me");
    });
  });
  return (
    <div className="App">
      <div className="container">
        <Form>
          <Contact setForm={setForm} form={form} moveNext={moveNext} />
          <SoftwareImplementation
            setForm={setForm}
            form={form}
            moveNext={moveNext}
          />
          <SoftwareLearn setForm={setForm} form={form} moveNext={moveNext} />
          <FiscalDevices />
          {/* <button onClick={handleShowData}>Pokaż dane</button> */}
        </Form>
      </div>
    </div>
  );
}

export default App;
