import $ from "jquery";
import React, { useState, useEffect } from "react";
import POS from "./POS";
import CHEF from "./CHEF";

const SoftwareImplementation = props => {
  const [modal, setModal] = useState(-1);
  const [form, setForm] = useState();
  const prefix = "software-implementation-";
  const offers = [
    { descripton: "", name: "S4H POS" },
    { descripton: "", name: "S4H CHEF" },
    { descripton: "", name: "S4H KDS" },
    { descripton: "", name: "S4H SORTING" },
    { descripton: "", name: "S4H CATERING" },
    { descripton: "", name: "S4HONLINE Zamów" },
    { descripton: "", name: "S4H MOBILE POS" },
    { descripton: "", name: "S4H POS LITE" },
    { descripton: "", name: "S4H CRM" },
    { descripton: "", name: "S4H Hotel&Spa" },
    { descripton: "", name: "S4H ONLINE MiniBarek" },
    { descripton: "", name: "S4H BOOKING" },
    { descripton: "", name: "S4H ONLINE CleanRoom" },
    { descripton: "", name: "S4H HANDEL" },
    { descripton: "", name: "S4H ONLINE PanelManager" },
    { descripton: "", name: "S4H BI" }
  ];
  const modalIdMapper = {
    0: (
      <POS
        setModal={setModal}
        id={modal}
        setForm={props.setForm}
        form={props.form}
      />
    ),
    1: <CHEF setModal={setModal} setForm={props.setForm} form={props.form} />
  };

  const offerItems = offers.map((offer, index) => {
    return (
      <div
        key={`${prefix}${index}`}
        className="offer col-4 mb-1 p-0 d-flex flex-column justify-content-between border mb-2"
      >
        <div className="title">
          <h3 className="text-center">{offer.name}</h3>
        </div>
        <div className="description p-2 text-center">{offer.description}</div>
        <div>
          <button
            type="button"
            className="btn btn-primary w-100"
            data-toggle="modal"
            data-target={`#sowtware-implementation-info-${index}`}
          >
            Zobacz informacje
          </button>
          <button
            data-target-id={index}
            className="btn btn-success w-100 configure"
            onClick={e => {
              e.preventDefault();
              setModal(index);
            }}
          >
            Dodaj
          </button>
          <div
            className="modal fade"
            id={`sowtware-implementation-info-${index}`}
            tabIndex="-1"
            role="dialog"
            aria-labelledby="modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="modalLabel">
                    {offer.name}
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">...</div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                  >
                    Zamknij
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });

  const handleMoveNext = () => {
    props.moveNext("#expand-software-implementation", "#expand-software-learn");
  };

  useEffect(() => {
    const setHideableElements = () => {
      const form = document.querySelector(".form");
      const expandButtons = form.querySelectorAll(".expand-button");
      const collapsed = form.querySelectorAll(".collapsed");
      const contactDiffrentPlaceAddresCheckbox = form.querySelector(
        "#diffrent-place-address"
      );
      const showElement = elementSelector => {
        const element = form.querySelector(elementSelector);
        $(element).toggle("slow");
      };
      const expandSection = event => {
        const expandItem = form.querySelector(
          event.target.getAttribute("data-target")
        );
        expandButtons.forEach(element => {
          if (element === event.target) {
            if (expandItem.classList.contains("collapsed")) {
              $(expandItem).show();
              expandItem.classList.remove("collapsed");
            } else {
              $(expandItem).hide();
              expandItem.classList.add("collapsed");
            }
          } else {
            const expandElement = form.querySelector(
              element.getAttribute("data-target")
            );
            if (!expandElement.classList.contains("collapsed")) {
              expandElement.classList.add("collapsed");
              $(expandElement).hide();
            }
          }
        });
      };

      contactDiffrentPlaceAddresCheckbox.addEventListener(
        "click",
        showElement.bind(null, ".place-address")
      );

      expandButtons.forEach(element => {
        element.addEventListener("click", expandSection);
      });

      collapsed.forEach(element => {
        $(element).hide();
      });
    };
    setHideableElements();
  });

  return (
    <div className="software-implementation">
      <button
        className="expand-button mb-2"
        type="button"
        data-target="#expand-software-implementation"
      >
        Oprogramowanie - wdrożenie
      </button>
      <div
        id="expand-software-implementation"
        className="row justify-content-between m-0  collapse-me"
      >
        {offerItems}
        <div className="w-100">
          <button className="btn btn-success mb-2" onClick={handleMoveNext}>
            Przejdź dalej
          </button>
        </div>
      </div>
      {modalIdMapper[modal]}
    </div>
  );
};

export default SoftwareImplementation;
