import $ from "jquery";
import React, { useEffect } from "react";

const Form = props => {
  useEffect(() => {
    const hiddenElements = document.querySelectorAll(".form .collapsed");
    hiddenElements.forEach(element => {
      $(element).hide();
    });
  });
  return (
    <div className="form">
      <form action="">{props.children}</form>
    </div>
  );
};

export default Form;
