import React, { useState, useEffect } from "react";
import Recipe from "./Recipe";

const CHEF = props => {
  window.onclick = function(event) {
    const modal = this.document.querySelector(".form .modal");

    if (event.target === modal) {
      props.setModal(-1);
    }
  };

  const [CHEF, setCHEF] = useState({ storages: 0, recipes: [] });
  const [showAddRecipeForm, setShowAddRecipeForm] = useState(false);
  const handleAddRecipe = () => {
    setShowAddRecipeForm(true);
  };
  const handleCloseModal = () => {
    props.setModal(-1);
  };
  const handleHideRecipeForm = () => {
    setShowAddRecipeForm(false);
  };
  const handleStorageChange = e => {
    setCHEF({ [e.target.name]: e.target.value });
  };

  const handleSaveData = () => {
    props.setForm({ ...props.form, CHEF });
  };

  useEffect(() => {
    document.body.style.overflow = "hidden";

    return () => {
      document.body.style.overflow = "auto";
    };
  });
  return (
    <div
      className="modal fade show"
      tabIndex="-1"
      role="dialog"
      id={`question-${props.id}`}
      style={{ display: "block" }}
      aria-modal="true"
    >
      <div className="modal-dialog" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">S4H CHEF</h5>
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={handleCloseModal}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <div className="form-group">
              <label htmlFor="storages">Liczba magazynów</label>
              <input
                id="storages"
                name="storages"
                className="form-control"
                type="number"
                value={CHEF.storages}
                onChange={handleStorageChange}
              />
            </div>
            <div className="form-group">
              {showAddRecipeForm ? (
                <Recipe
                  hideRecipeForm={handleHideRecipeForm}
                  setCHEF={setCHEF}
                  CHEF={CHEF}
                />
              ) : (
                <button className="btn btn-info" onClick={handleAddRecipe}>
                  Dodaj recepturę
                </button>
              )}

              {props.products ? (
                <button className="btn btn-info">
                  Dodaj receptury do istniejących artykułów
                </button>
              ) : null}
            </div>
          </div>

          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-dismiss="modal"
              onClick={handleCloseModal}
            >
              Anuluj
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => {
                handleSaveData();
                handleCloseModal();
              }}
            >
              Zapisz zmiany
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CHEF;
