import $ from "jquery";
import React, { useState } from "react";

const Contact = props => {
  const companyName = "company-name",
    companyAddress = "company-address",
    placeAddress = "place-address",
    nip = "nip",
    cutomerName = "name",
    surname = "surname",
    email = "email",
    phone = "phone",
    diffrentPlaceAddress = "diffrent-place-address",
    nipLength = 10;

  const validateMessages = [
    { name: companyName, errorMessage: "" },
    { name: companyAddress, errorMessage: "" },
    { name: nip, errorMessage: "" },
    { name: cutomerName, errorMessage: "" },
    { name: surname, errorMessage: "" },
    { name: email, errorMessage: "" },
    { name: phone, errorMessage: "" },
    { name: placeAddress, errorMessage: "" }
  ];
  const [contactInfo, setContactInfo] = useState({
    [companyName]: "",
    nip: "",
    [companyAddress]: "",
    [placeAddress]: "",
    name: "",
    surname: "",
    email: "",
    phone: "",
    [diffrentPlaceAddress]: false
  });
  let isValid = true;
  const saveData = () => {
    props.setForm({ ...props.form, contactInfo });
  };
  const handleInputChange = event => {
    if (event.currentTarget.getAttribute("type") === "checkbox") {
      setContactInfo({
        ...contactInfo,
        [diffrentPlaceAddress]: event.currentTarget.checked
      });
    } else {
      setContactInfo({
        ...contactInfo,
        [event.currentTarget.name]: event.currentTarget.value
      });
    }
  };
  const handleMoveNext = event => {
    event.preventDefault();
    // isValid = validate();
    if (isValid) {
      saveData();
      props.moveNext("#expand-contact", "#expand-software-implementation");
    } else {
    }
    showValidate();
  };

  const showValidate = () => {
    validateMessages.forEach(element => {
      const { name, errorMessage } = element;
      const messagePlace = document.querySelector(`.form [name="${name}"]`)
        .parentElement;

      let span = messagePlace.querySelector("span");
      if (!span) {
        span = document.createElement("span");
        messagePlace.appendChild(span);
      }

      span.classList.add("validator");
      span.innerHTML = errorMessage;
    });
  };

  const validate = () => {
    validateMessages.forEach(element => {
      const { name } = element;
      const value = contactInfo[name];
      switch (name) {
        case companyName:
        case cutomerName:
        case surname:
        case phone:
        case companyAddress:
          if (!value.trim()) {
            element.errorMessage = "To pole nie może być puste";
            break;
          } else {
            element.errorMessage = "";
            break;
          }
        case nip:
          if (value.length < nipLength) {
            element.errorMessage = "Za krótki nr NIP";
            break;
          } else if (value.length > nipLength) {
            element.errorMessage = "Za długi nr NIP";
            break;
          } else {
            element.errorMessage = "";
            break;
          }
        case diffrentPlaceAddress:
        case placeAddress:
          if (contactInfo[diffrentPlaceAddress]) {
            if (!value.trim()) {
              element.errorMessage = "To pole nie może być puste";
              break;
            } else {
              element.errorMessage = "";
              break;
            }
          } else {
            element.errorMessage = "";
            break;
          }
      }
    });

    for (let i = 0; i < validateMessages.length; i++) {
      if (validateMessages[i].errorMessage !== "") {
        isValid = false;
      } else {
        isValid = true;
      }
    }
  };

  return (
    <div className="contact">
      <button
        className="expand-button mb-2"
        type="button"
        data-target="#expand-contact"
      >
        Informacje kontaktowe
      </button>
      <div id="expand-contact" className="expand-section">
        <div className="company-name form-group">
          <label htmlFor={companyName}>*Nazwa firmy:</label>
          <input
            className="form-control"
            id={companyName}
            name={companyName}
            type="text"
            value={contactInfo[companyName]}
            onChange={handleInputChange}
          />
        </div>
        <div className="nip form-group">
          <label htmlFor="nip">*NIP:</label>
          <input
            className="form-control"
            id="nip"
            name="nip"
            type="text"
            value={contactInfo.nip}
            max-length="10"
            onChange={handleInputChange}
          />
        </div>
        <div className="company-address form-group">
          <div>
            <input
              type="checkbox"
              name="diffrent-place-address"
              id="diffrent-place-address"
              className="mr-1"
              value={contactInfo[companyAddress]}
              onChange={handleInputChange}
            />
            <label htmlFor="diffrent-place-address">
              Adres lokalu jest inny niż adres siedziby?
            </label>
          </div>

          <label htmlFor={companyAddress}>*Adres siedziby:</label>
          <input
            className="form-control"
            id={companyAddress}
            name={companyAddress}
            type="text"
            onChange={handleInputChange}
          />
        </div>
        <div className="place-address form-group collapse">
          <label htmlFor={placeAddress}>*Adres lokalu:</label>
          <input
            className="form-control"
            id={placeAddress}
            name={placeAddress}
            type="text"
            value={contactInfo[placeAddress]}
            onChange={handleInputChange}
          />
        </div>
        <div className="name form-group">
          <label htmlFor="name">*Imię:</label>
          <input
            className="form-control"
            id="name"
            name="name"
            type="text"
            value={contactInfo["name"]}
            onChange={handleInputChange}
          />
        </div>
        <div className="surname form-group">
          <label htmlFor="name">*Nazwisko:</label>
          <input
            className="form-control"
            id="surname"
            name="surname"
            type="text"
            value={contactInfo["surname"]}
            onChange={handleInputChange}
          />
        </div>
        <div className="email form-group">
          <label htmlFor="email">Email:</label>
          <input
            className="form-control"
            id="email"
            name="email"
            type="email"
            value={contactInfo["email"]}
            onChange={handleInputChange}
          />
        </div>
        <div className="phone form-group">
          <label htmlFor="phone">*Telefon:</label>
          <input
            className="form-control"
            id="phone"
            name="phone"
            type="text"
            onChange={handleInputChange}
          />
        </div>
        <div className="message form-group">
          <label htmlFor="message">Wiadomość:</label>
          <textarea
            className="form-control"
            id="message"
            name="message"
          ></textarea>
        </div>
        <button className="btn btn-success mb-2" onClick={handleMoveNext}>
          Przejdź dalej
        </button>
      </div>
    </div>
  );
};

export default Contact;
